body {
	background-color: orange;
	margin: 20px;
}

.success {
	margin: 0 auto;
	height: 100vh;
}

.print {
	float: left;
}

label {
	font-weight: bolder;
}

.user-option {
	display: none;
}

.App {
	width: 90%;
	margin: 0 auto;
}

select {
	margin-bottom: 5px;
	width: 100%;
	padding: 5px;
	border-radius: 5px;
	border: 1px solid #BDC7D8;
}

.errorMsg {
	color: red;
	font-size: 14px;
}

.sub-heading {
	color: #fff;
	font-weight: bold;
}
.infoMsg {
	color: red;
	text-align: center;
}

.intro {
	font-family: "Times New Roman";
	font-size: 24px;
	line-height: 1.5;
	color: rgba(0,0,0, 0.7);
	font-weight: bold;
	font-style: italic;
}

.author::before {
	content: '...'
}
